<template>
  <div class="myOrderList" :class="{themeL:bindThemeL}">
    <!-- <van-nav-bar class="title" title="售后" @click-left="goToBack()" left-arrow :style="setTopHeight"></van-nav-bar> -->
    <navigation-bar class="title" @onLeftClick="goToBack()" pageName="售后" :style="setTopHeight"/>
    <div class="tabTitle">
      <van-tabs v-model="active" color="#FB5768" @click="controlTab" line-width="0.67rem" line-height="0.08rem">
        <van-tab v-for="(tit,i) in tabTitle" :title="tit" :key="i" :title-style="{fontSize: titleThemeL}">
        </van-tab>
      </van-tabs>
    </div>
    <van-swipe class="orderSwipe" :show-indicators="false" :loop="false" @change="controlSwiper" ref="swipe"
               :initial-swipe="afterTab">

      <van-swipe-item :key="0" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"
                      @touchend="bottomAdTouchEnd()">
        <afterSaleCard ref="mychild0" mgChildKey="0" :isActKey="active" :isClickEven="swipeClickedEven">

        </afterSaleCard>
      </van-swipe-item>

      <van-swipe-item :key="1" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"
                      @touchend="bottomAdTouchEnd()">
        <afterSaleCard ref="mychild1" mgChildKey="1" :isActKey="active" :isClickEven="swipeClickedEven">

        </afterSaleCard>
      </van-swipe-item>
      <van-swipe-item :key="2" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"
                      @touchend="bottomAdTouchEnd()">
        <afterSaleCard ref="mychild2" mgChildKey="2" :isActKey="active" :isClickEven="swipeClickedEven">

        </afterSaleCard>
      </van-swipe-item>

    </van-swipe>
  </div>
</template>

<style scoped="scoped" lang="scss">
  .themeL{

  }
  .myOrderList {
    width: 100%;
    background: #f9f9f9;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .myOrderList .title {
    z-index: 100;
    width: 100%;
  }

  .myOrderList .commonHead {
    width: 25px;
    padding: 20px 20px 10px 20px;
    float: left;
  }

  .myOrderList .orderTitle {
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #333333;
    letter-spacing: -0.06px;
    float: right;
    padding-top: 20px;
    padding-right: 167px;
  }

  .tabTitle {
    width: 100%;
    /* z-index: 100; */
    background: #FFFFFF;
    /* margin-top: 1px; */
  }

  .orderSwipe {
    overflow-x: hidden;
    height: 100%;
  }

  /deep/ .van-swipe {
    overflow-y: auto !important;
  }

  /deep/ .van-tabs {
    position: relative;
    padding: 1px 50px;
    line-height: 60px !important;
  }

  /deep/ .van-tab {
    line-height: 52px !important;
  }

  /deep/ .van-hairline--top-bottom {
    height: 1.33333rem !important;
    line-height: 1.33333rem !important;
    border: none !important;
  }

  /deep/ .van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after {
    border-width: 0 0;
  }

  /deep/ .van-tabs__line {
    position: absolute;
    bottom: 20px !important;
    left: 0;
    z-index: 1;
    height: 0.08rem;
    background-color: #ee0a24;
    border-radius: 0.08rem;
  }
</style>

<script>
  import afterSaleCard from './afterSaleCard.vue';
  import NavigationBar from '@/common/currency/NavigationBar.vue';

  export default {
    name: 'afterSaleList',
    components: {afterSaleCard, NavigationBar},
    data: function () {
      return {
        tabTitle: ['退货申请', '处理中', '已完成'],
        active: 0,
        key: 0,
        afterTab: 0,//用来判断返回上一页到售后列表
        setTopHeight: {
          'padding-top': this.$stbHeight + 'px'
        },
        swipeClickedEven: true,
        /*大字模式*/
        bindThemeL: this.$curTheme == 0 ? false : true,
        titleThemeL: this.$curTheme == 0 ? "14px" : "17px"
      }
    },
    methods: {
      bottomAdTouchStart: function () {
        console.log('touch start');
        this.adClickFlag = true;
      },
      bottomAdTouchMove: function () {
        console.log('touch Move');
        this.adClickFlag = false;
      },
      bottomAdTouchEnd: function (adItem) {
        if (this.adClickFlag) {
          console.log('点击事件');
          this.swipeClickedEven = true;
        } else {
          console.log('滑动touch事件')
          this.swipeClickedEven = false;
        }
      },
      //通过滑动和点击切换订单类别
      controlTab(index) {
        this.key = index;
        this.$refs.swipe.swipeTo(index);
      },
      controlSwiper(index) {
        this.active = index;
        if (index == '0') {
          this.$refs.mychild0.onRefresh('0');
          this.$refs.mychild1.emptyData();
          this.$refs.mychild2.emptyData();
        } else if (index == '1') {
          this.$refs.mychild1.onRefresh('1');
          this.$refs.mychild0.emptyData();
          this.$refs.mychild2.emptyData();
        } else {
          this.$refs.mychild2.onRefresh('2');
          this.$refs.mychild1.emptyData();
          this.$refs.mychild0.emptyData();
        }
      },
      goToBack() {
        this.$router.go(-1);//返回上一层
      },
      //设置离开订单列表时的tab
      setTabIndex(afterTabIndex) {
        this.$store.commit("leaveOrderList", {
          afterTabIndex: afterTabIndex,
        })
      }
    },
    beforeRouteLeave(to, from, next) {
      if (to.name == 'afterSaleApply' || to.name == 'afterSaleDetail' || to.name == 'shopPage') {
        this.setTabIndex(this.active);
      } else {
        this.setTabIndex(10);
      }
      next();
    },
    mounted: function () {
      if (this.afterTabIndex == 0 || this.afterTabIndex == 1 || this.afterTabIndex == 2) {
        this.controlSwiper(this.afterTabIndex);
        this.afterTab = this.afterTabIndex;
      } else {
        this.controlSwiper('0');
        this.afterTab = 0;
      }
    },
    computed: {
      afterTabIndex() {
        return this.$store.state.productPage.afterTabIndex;
      }
    }
  }
</script>
