import http from "../http";


//获取我的订单列表(不包括待评价订单列表)
const getAllOrders = (reqParam) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-commerce-sale/myOrder/getAllOrders', reqParam,'baseUrl',true).then(result => {
             resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}
// 获取待评价订单列表
const getunevaluatedOrders =(reqParam) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-commerce-sale/myOrder/getUnevaluatedSaleOrders', reqParam,'baseUrl',true).then(result => {
             resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
// 确认收货
    const confirmOrder =(reqParam) => {
        return new Promise((resolve, reject) => {
            http.post('xwj-commerce-sale/myOrder/confirmOrder', reqParam,false).then(result => {
                 resolve(result);
            }).catch(error => {
                reject(error);
            })
        });
    }
//获取售后列表
    const getAfterSaleList =(reqParam) => {
        return new Promise((resolve, reject) => {
            http.get('xwj-commerce-sale/goods/returnOrderList',reqParam,'baseUrl',false).then(result => {
                 resolve(result);
            }).catch(error => {
                reject(error);
            })
        });
    }
//获取优惠券列表
const getUserCouponList =(reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-commerce-coupon/coupon/queryByUser',reqParam,false,'baseUrl',false).then(result => {
             resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
// 取消未付款订单
const cancelOrder =(reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-commerce-sale/order/orderCancel',reqParam).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
//准备支付
const prepareToPayOrder =(reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-commerce-sale/order/prepareToPayOrder',reqParam).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
//订单列表-再次购买
const RepurchaseOrder =(reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-commerce-sale/shopCart/batchAddGoods',reqParam).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
//订单列表页面申请售后按钮校验(新增)
const applyForAfter =(reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-commerce-sale/goods/validateReturnGoods',reqParam).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
//取消申请售后
    const cancelApply =(reqParam) => {
        return new Promise((resolve, reject) => {
            http.post('xwj-commerce-sale/goods/returnGoodsCancel',reqParam).then(result => {
                resolve(result);
            }).catch(error => {
                reject(error);
            })
        });
    }
// 申请售后
    const applyForAfterSale =(reqParam) => {
        return new Promise((resolve, reject) => {
            http.post('xwj-commerce-sale/goods/returnGoods',reqParam).then(result => {
                resolve(result);
            }).catch(error => {
                reject(error);
            })
        });
    }
//待收货状态里的申请退款
    const applyRefund=(reqParam) => {
        return new Promise((resolve, reject) => {
            http.post('xwj-commerce-sale/goods/refundBySub',reqParam).then(result => {
                resolve(result);
            }).catch(error => {
                reject(error);
            })
        });
    }

    export {
        getAllOrders,
        getunevaluatedOrders,
        confirmOrder,
        getAfterSaleList,
        getUserCouponList,
        cancelOrder,
        RepurchaseOrder,
        prepareToPayOrder,
        cancelApply,
        applyForAfterSale,
        applyForAfter,
        applyRefund
    }
