<template>
  <div :class="{themeL:bindThemeL}">
    <van-pull-refresh v-model="isRefresh" @refresh="onRefresh">
      <van-list
          v-model="loading"
          :finished="finished"
          @load="onLoad(mgChildKey)"
          error-text="请求失败，刷新重新加载"
          :immediate-check="false"
          :style="{'min-height': (listMinHeight+'px')}"
      >
        <div class="orderList" v-for="(item ,i) in lists" v-if="isLists==false">
          <div class="commonShop">
            <div class="shopTitle" @click="toShop(item.shopCode)">
              <div class="shopName">{{item.shopName}}</div>
              <img :src="img.arrowShop">
            </div>
            <!--售后状态-->
            <div class="callShop" v-if="mgChildKey==1&&item.billStatus==0">待审核</div>
            <div class="callShop" v-if="mgChildKey==1&&item.billStatus==1">待退款</div>
            <div class="callShop" v-if="mgChildKey==2&&item.billStatus==0">退款成功</div>
            <div class="callShop" v-if="mgChildKey==2&&item.billStatus==1">申请未通过</div>

            <div class="normalCard" @click="showAfterDetail(item)">
              <img class="goodsPic" :src="item.spuPicUrl"/>
              <!--商品描述-->
              <div class="goodsDetail">
                <div class="spuName">{{item.spuName}}</div>
                <div class="spuAttr">{{item.attrValueContent}}</div>
                <div class="orderCount">x{{item.skuCount}}</div>
              </div>

              <div class="orderSC">
                <span class="redMoneyIcon">￥</span>
                <span class="redMoney">{{item.returnRealTotal}}</span>
              </div>
            </div>
            <!--<div class="orderDetail" >-->
            <!--<span class="redMoney">￥{{item.realPayTotal}}</span>-->
            <!--</div>-->
            <div class="bottomBtn" v-if="mgChildKey==0">
              <button class="turnToPay" @click="applyForAfter(item)">申请售后</button>
            </div>

          </div>
        </div>
        <!--无订单时的显示图片-->
        <div class="noList" v-if="isLists==true">
          <img :src="img.noList"/>
          <div>暂无订单</div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<style scoped="scoped" lang="scss">
  .themeL{
    .shopTitle {
      padding: 10px 15px;
      /*height: 20px;*/
      font-family: PingFangSC-Medium;
      font-size: 17px;
      color: #333333;
      letter-spacing: -0.04px;
      width: 65%;
      float: left;

      img {
        width: 6px;
        height: 11px;
        padding-top: 2px;
        padding-left: 5px;
      }
    }
    .callShop {
      font-family: PingFangSC-Regular;
      font-size: 15px;
      color: #333333;
      letter-spacing: -0.04px;
    }
    .commonShop{
      margin: 15px 0px;
      border-radius: initial;
    }
    .goodsPic {
      margin-right: 10px;
    }
    .goodsDetail {
      width: 170px;
    }
    .spuName {
      width: 170px;
      font-size: 17px;
    }
    .spuAttr {
      font-size: 14px;
    }
    .orderCount {
      font-size: 14px;
    }
    .orderSC {
      margin-top: 0px;
    }
    .redMoneyIcon{
      font-size: 14px;
    }
    .redMoney{
      font-size: 20px;
    }
  }
  .commonShop {
    margin: 15px;
    background: #FFFFFF;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 5px;
    overflow: hidden;
  }

  .shopTitle {
    padding: 10px 15px;
    /*height: 20px;*/
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.04px;
    width: 63%;
    float: left;
    box-sizing: border-box;

    img {
      width: 6px;
      height: 11px;
      padding-top: 2px;
      padding-left: 2px;
    }
  }

  .shopName {
    float: left;
    max-width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .normalCard {
    margin-left: 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    display: flex;
    width: 92%;
    justify-content: flex-start;
    border-top: 1px solid #e5e5e5;
  }

  .goodsPic {
    width: 74px !important;
    height: 74px;
    /*margin: 2px;*/
    margin-right: 12px;
    display: inline-block; /*行内块元素*/
    white-space: nowrap;
    /*float: left;*/
  }

  .orderDetail {
    padding: 15px 15px;
    float: right;
    width: 100%;
    font-family: PingFangSC-Regular;
    font-size: 12px;
