  .callShop img {
    vertical-align: middle;
    width: 14px;
  }

  .goodsDetail {
    /*float: right;*/
    /*display: inline-block;*/
    width: 162px;
    height: 44px;
    /*position: relative;*/
    /*left: -90px;*/
  }

  .spuName {
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.04px;
    width: 162px;
    display: block;
    height: 44px;
    line-height: 22px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    word-break: break-all;
    word-wrap: break-word;
  }

  .spuAttr {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #333333;
    letter-spacing: -0.04px;
    padding-top: 4px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    float: left;
    max-width: 130px;
    padding-right: 5px;
  }

  .orderSC {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #9A9A9A;
    letter-spacing: -0.04px;
    width: 50px;
    /* text-align:right; */
    margin-top: 53px;
  }

  .orderStatus {
    display: block;
  }

  .orderCount {
    margin-top: 4px;
    display: block;
    /* text-align: right; */
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #9A9A9A;
    letter-spacing: -0.04px;
    /* max-width: 130px; */
  }

  .noList img {
    margin: 100px 100px 10px 100px;
    width: 180px;
    display: inline-block;
  }

  .noList div {
    text-align: center;
    width: 100%;
    font-size: 14px;
    color: #9A9A9A;
  }
  .themeL .noList div {
    font-size: 15px;
  }

  /deep/ .van-list__loading {
    display: none;
  }

  /deep/ .van-pull-refresh__head {
    position: absolute;
    top: -65px;
    left: 0;
    width: 100%;
    height: 1.33333rem;
    overflow: hidden;
    color: #969799;
    font-size: 0.37333rem;
    line-height: 1.33333rem;
    text-align: center;
  }
</style>

<script>
  import {getAfterSaleList} from "../../services/orderListService";
  import {mapActions, mapState} from "vuex";
  import Vue from 'vue';

  export default {
    name: 'afterSaleCard',
    props: ['mgChildKey', 'isActKey', 'isClickEven'],
    data: function () {
      return {
        lists: [],
        isLists: false,
        img: {
          arrowShop: require("@/assets/images/mall/mall_icon_more.png"),
          callShop: require("@/assets/images/mall/call_small.png"),
          noList: require("@/assets/images/default/no_after_sales_img.png"),//img里面没有找到
        },
        loading: false,//上拉加载状态
        finished: false,//上拉加载无数据时的状态
        page: 1,//当前接口请求的页数
        totalPages: 5,//订单列表的总页数，先设置一个大于page的初始值
        isRefresh: false,//是否处于刷新状态
        listMinHeight: document.body.clientHeight - this.$stbHeight - 55 - 44,
        ifDetailReturn: false,
        /*大字模式*/
        bindThemeL: this.$curTheme == 0 ? false : true
      }
    },
    methods: {
      // 下拉刷新
      onRefresh(type) {
        this.page = 1;
        this.returnOrderList(type);
        this.finished = false;
      },
      //清空数据
      emptyData() {
        var arr = [];
        arr.push(this.lists[0])
        this.lists = [];
      },
      //退货列表查询
      returnOrderList(type) {
        const _this = this;

        const data = {
          page: _this.page,
          row: 5,
          // ownerCode: '2019110817314234128',
          ownerCode: _this.$userInfo.OWNER_CODE,
          returnStatus: _this.mgChildKey,
        }
        getAfterSaleList(data).then(function (response) {
          _this.totalPages = response.pages;
          if (_this.page == 1) { //如果是下拉刷新，从第一页开始刷新
            _this.lists = [];
          }
          if (response.total == 0) {
            _this.isLists = true;
            _this.finished = false;
            _this.loading = false;
            _this.isRefresh = false;
          } else {
            _this.lists = _this.lists.concat(response.data);
            _this.lists.map((item, index) => {
              //图片本地化存储
              item.picUrl = item.spuPicUrl;
              Vue.prototype.$isHasLocalImage(item.spuPicUrl, function (localImgUrl) {
                item.spuPicUrl = localImgUrl;
              })
            });
            _this.page += 1;
            _this.loading = false;
            _this.finished = _this.page > _this.totalPage ? true : false;
            _this.isRefresh = false;
          }
        });
      },
      onLoad(type) {
        const _this = this;
        if (_this.mgChildKey != _this.isActKey) {
          return
        }
        console.log("初始化的时候走了这里");
        if (_this.totalPages >= _this.page) {
          _this.returnOrderList(type);
        } else if (_this.totalPages < _this.pageisLists || _this.lists != '') {
          _this.finished = true;
        }
      },
      // fresh(){
      //   const _this=this;
      //   if(_this.isRefresh==true){
      //     _this.returnOrderList();
      //     _this.finished=false;
      //   }
      // },
      //申请售后跳转按钮
      applyForAfter(item) {
        if (!this.isClickEven) {
          return;
        }
        this.$router.push({name: 'afterSaleApply', params: {detail: item}});
      },
      //售后详情
      showAfterDetail(item) {
        if (!this.isClickEven) {
          return;
        }
        if (this.mgChildKey != 0) {
          this.$router.push({name: 'afterSaleDetail', params: {detail: item, flag: this.mgChildKey}});
        }
      },
      //跳转店铺首页
      toShop(shopCode) {
        if (!this.isClickEven) {
          return;
        }
        this.$router.push({name: 'shopPage', params: {shopCode: shopCode}});
      },
    },

  }
</script>
